export default {
  data() {
    return {
      items: [
        {
          value: 'USA',
          name: '美国'
        },
        {
          value: 'CHN',
          name: '中国',
          checked: 'true'
        },
        {
          value: 'BRA',
          name: '巴西'
        },
        {
          value: 'JPN',
          name: '日本'
        },
        {
          value: 'ENG',
          name: '英国'
        },
        {
          value: 'FRA',
          name: '法国'
        }
      ],
      current: 0
    };
  },
 methods: {
   radioChange: function(e) {
     console.log(e.detail.value)
     this.$emit('change', e);
     for (let i = 0; i < this.items.length; i++) {
       if (this.items[i].value === e.detail.value) {
         this.current = i;
         break;
       }
     }
   }
 }
};
